import styled from "styled-components";

export const NavbarCard = styled.nav`
position: fixed;
width: 350px;
height: 694px;
background-color: #040B14;
border-radius: 0 10px 10px 0;
`

export const ImageDiv = styled.div`
margin-left: 88px;
img{
    margin-top: 20px;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 10px solid #2C2F3F;
}
h1{
    text-align: center;
    color: white;
    padding-right: 100px;
    font-size: 24px;
}
`

export const Links = styled.div`
display: flex;
justify-content: space-evenly;
font-size: 20px;
font-weight: bold;
a{
color: white;
    text-decoration: none;
div{
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 50%;
    background-color: #212431;
    cursor: pointer;
    transition-duration: .5s;
    p{
        margin: 0;
        padding: 0;
        /* margin: 10px; */
        /* margin-bottom: 13px; */
        margin-top: 13px;
        svg{
            width: 20px;
            height: 20px;
            /* margin-top: 5px; */
            fill: white;
        }
    }
    &:hover{
        background-color: #149DDD;
    }
}
}
`

export const ID = styled.div`
width: 250px;
margin: 12px 0;
margin-left: 80px;
a{
    display: flex;
    text-decoration: none;
    color: #687180;
    transition-duration: .5s;
    &:hover{
        color: white;
        svg{
            
        fill: #0C9DDD;
        }
    }
    svg{
        width: 30px;
        height: 30px;
        fill: #687180;
    transition-duration: .5s;
    }
    b{
        padding-top: 5px;
        margin-left: 15px;
    }
}
`